import React from 'react';
import img_form_icon from "./18.jpg";



class SetUser extends React.Component{
    constructor(props) {
        super(props);
      }
    render(){
        const { form, error } = this.props.state;
        return (
            <form name="form" onSubmit={this.props.handleSetuser}>
                <span class="form-title">
                    Welcome !
                </span>
                <span class="form-icon">
                    <img src={img_form_icon} alt="Rice"/>
                </span>
                <div className="field">
                    <label>Account name</label>
                    <input
                        type="text"
                        name="username"
                        placeholder="All small letters, a-z, 1-5 or dot, max 12 characters"
                        onChange={this.props.handleChange}
                        pattern="[\.a-z1-5]{2,12}"
                        required
                        autoComplete="off"
                    />
                </div>
                <div className="field">
                    <label>Private key</label>
                    <input
                        type="password"
                        name="key"
                        value={form.key}
                        onChange={this.props.handleChange}
                        pattern="^.{51,}$"
                        required
                        autoComplete="new-password"
                    />
                </div>
                <div className="field form-error">
                    {error && <span className="error">{error}</span>}
                </div>
                <div className="bottom">
                    <Button type="submit" className="green">
                        {"SET USER and KEY"}
                    </Button>
                </div>
            </form>
        );



    }
}
const Button = ({ ...props }) => {
    const { className, type, style, onClick, children } = props;
    return (
      <button
        className={`Button${ className ? ' ' + className : '' }`}
        { ...{ type, style, onClick } }
      >{ children }</button>
    );
  }
export default SetUser;
