import { Nav,Button,Modal} from 'react-bootstrap';
import React from 'react';
import {GetUserInf}from './Action';


class TopNav  extends React.Component {
    constructor(props){
        super(props);
        
    }
    setinsetuser(params){
        this.props.setuserin(params);
    }
   
    render(){
        return(
            <>
            <GetUserInf user_inf_rerand={this.props.user_inf_rerand} reranded_inf={this.props.reranded_inf}/>
            <Nav className="justify-content-center" variant="pills" defaultActiveKey="/home">
                
                    
                
                <Nav.Item>
                    <Button onClick={this.setinsetuser.bind(this,"setuser")} type="submit" variant="outline-primary">
                        {"SET USER "}
                    </Button>
                </Nav.Item>
                <Nav.Item>
                    <Button onClick={this.setinsetuser.bind(this,"Infs")} type="submit" variant="outline-primary">
                        {"Infs "}
                    </Button>
                </Nav.Item>
                <Nav.Item>
                <Button onClick={this.setinsetuser.bind(this,"bet")} type="submit" variant="outline-primary">
                        {"BET "}
                    </Button>
                </Nav.Item>
                <Nav.Item>
                <Button onClick={this.setinsetuser.bind(this,"redeem")} type="submit" variant="outline-primary">
                        {"REDEEM "}
                    </Button>
                </Nav.Item>
                <Nav.Item>
                <Button onClick={this.setinsetuser.bind(this,"mytickets")} type="submit" variant="outline-primary">
                        {"My Tickets "}
                    </Button>
                </Nav.Item>
                <Nav.Item>
                <Button type="submit" variant="outline-primary">
                        {"BET "}
                </Button>
                </Nav.Item>
                
              </Nav>
              </>
        );
    }
    
}




export default TopNav;