import { Nav,Button,Modal,Spinner} from 'react-bootstrap';
import React from 'react';


class Popoutwindow extends React.Component {
    
    constructor(props){
        super(props);
        
        
    }
    
    
  
    render(){
    
    return (
      <>
        <Modal show={this.props.show} onHide={this.props.handleClose}>
          <Modal.Header closeButton>
            <Modal.Title>{this.props.title}</Modal.Title>
          </Modal.Header>
          <Modal.Body>{process_transection_obj(this.props.console_obj)}</Modal.Body>
          <Modal.Footer>
            <Button variant="primary" onClick={this.props.handleClose}>
            Close
            </Button>
          </Modal.Footer>
        </Modal>
      </>
    );
  }
}
function process_transection_obj(transection) {
      if(transection.passinf=='Loading'){
        return(
          <Spinner animation="border" role="status">
            <span className="sr-only">Loading...</span>
          </Spinner>
        );
      }
      if(transection.passinf=='error'){
        return(
          <p>error</p>
        );
      }
      return(
        JSON.stringify(transection)
      );
}
export default Popoutwindow;