import { Nav,Button,Modal,Table} from 'react-bootstrap';
import React from 'react';
import ApiService from './API';


class Infs  extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            console_block_string:'',
            returnobj:new Object(),
            finded:false,
        }
        ApiService.get_allow_table()
        .then((mesg) => {
            console.dir(mesg);
            
            //this.setState({console_block_string:JSON.stringify(mesg.rows)})
            this.setState({returnobj:mesg.rows});
            this.setState({finded:true});
            
        })
        .catch(err => {
            console.dir(err);
            this.setState({console_block_string:"some thing was wrong please try it again"});
        });
    }
    render(){
        
        return(
            <div className="Infs">
                <h3>
                    History Period
                </h3>
                <AllowTable state={this.state}/>
            </div>
        );
    }
}
class AllowTable extends React.Component {
    constructor(props){
        super(props);
        

    }
    render(){
        if(! this.props.state.finded){
            return(
                <p>loading</p>
                );
        }
        if(this.props.state.finded){
        let t=[]
        for(const item of this.props.state.returnobj){
            if(item.status==0){
                t.push(
                    <tr>
                      <td>{item.period}</td>
                      <td>{item.time}</td>
                      <td>{JSON.stringify(item.numbers)}</td>
                      <td>{JSON.stringify(item.people_win_prize)}</td>
                      <td>{item.currenpeoplet_balance}</td>
                    </tr>
                );
            }
            
        }
        return(
        <div className="AllowTable_table">
            <Table striped bordered hover>
              <thead>
                <tr>
                  
                  <th>Period</th>
                  <th>Time</th>
                  <th>Numbers</th>
                  <th>People Win</th>
                  <th>Balance</th>
                </tr>
              </thead>
              <tbody>
                {t}
              </tbody>
            </Table>
        </div>
        );
    }
    }
}
export default Infs;
