import React from 'react';
import ReactDOM from 'react-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import TopNav from './TopNav'
import { ActionBlock2,ConsoleOfTable,SetUser,GetCurrentPeriod,GetUserInf,Redeemall,ActionBet,Infs} from './Action';
import "./index.css";
import img_18 from "./img/18.jpg";
import img_co from "./img/co.png";
import img_fa from "./fabg.jpg";

class Manage extends React.Component{
    constructor(props) {
        super(props);
        // Inherit constructor
        // State for form data and error message
        this.state = {
            form: {
                username: '',
                key: '',
                numbers:'',
                period:'',
                gameid:0,
                error: '',
                },
            console_block_string:'welcome ',
            userset:false,
            user_inf_rerand:false,
            userin:"setuser",
            inredeem:false,
            inbet:false,
            ininfs:false,
            inmyticket:false,

        }
        this.handleChange = this.handleChange.bind(this);
        this.handleSetuser = this.handleSetuser.bind(this);
       
      }
    handleChange(event) {
        const { name, value } = event.target;
        const { form } = this.state;

        this.setState({
            form: {
                ...form,
                [name]: value,
                error: '',
            },
        });
    }
    handleSetuser(event){
        event.preventDefault();
        const { form } = this.state;
        sessionStorage.setItem("User_EOS_key", form.key);
        sessionStorage.setItem("User_EOS_account", form.username);
        this.setState({
            userset:true,
            userin:"bet",
        });
    }
    

    render(){
        const { console_block_string } = this.state;
        let shows=[];
        let nav=(
            <div id="menu">
            <TopNav setuserin={(parms)=>this.setState({userin:parms})} user_inf_rerand={this.state.user_inf_rerand} reranded_inf={()=>this.setState({user_inf_rerand:false})}/>
            </div>
            )
        switch(this.state.userin){
            case "setuser":
                return(
                    <>
                    {nav}
                    <main>
                        
                <div class="utlt">
                <div class="wrap-login100">
                <SetUser handleSetuser={(event)=>this.handleSetuser(event) } handleChange={(event)=>this.handleChange(event)} state={this.state}/>
                </div>
                <h2> testing_account:<tr></tr>player</h2>
                <h2> testing_key:5HxM6iGfYDYTLHDgRruBhbzzSsxndxfLXXswq4C1AS8gkLVGz3E</h2>
                </div>
                </main>
                </>
                );
            case "Infs":
                return(
                    <>
                    {nav}
                    <main>
                        
                        <Infs/>
                    </main>
                    </>
                )
            case "bet":
                return(
                    <>
                    {nav}
                    <main>
                        
                        
                        <ActionBet handleChange={(event)=>this.handleChange(event)} state={this.state} reset_userinf={()=>this.setState({user_inf_rerand:true})}/>
                        
                        <Consoleblock value={this.state.console_block_string} />
                        
                        
                        </main>
                    </>
                );
            case "mytickets":
                return(
                    <>
                    {nav}
                    <main>
                        
                        <ConsoleOfTable />
                    </main>
                    </>
                );
            case "redeem":
                return(
                    <>
                    {nav}
                    <main>
                        
                        <ActionBlock2 setConsole={(parms)=>this.setState({console_block_string:parms})} handleChange={(event)=>this.handleChange(event)} state={this.state}/>
                        <Redeemall setConsole={(parms)=>this.setState({console_block_string:parms})} />
                        <Consoleblock value={this.state.console_block_string} />
                    </main>
                    </>
                );
            default:
                return(
                    <main></main>
                );
        };
        
        
    }
}
class  Consoleblock extends React.Component {
    render(){
    return(
            <h3 id="CONTEX">
                {this.props.value}
            </h3>
        )
    }
}


ReactDOM.render(
    <Manage />,
    document.getElementById('root')
);


