import React from 'react';
import ApiService from './API';
import Popoutwindow from './popoutwindow'
import { Form,Button,} from 'react-bootstrap';


class  ActionBet extends React.Component {
    constructor(props) {
        // Inherit constructor
        super(props);
        // State for form data and error message
        
        // Bind functions
        this.state={
            pop_show:false,
            pop_console:'',
            returnobj:new Object(),
        }
        
        this.handleSubmit = this.handleSubmit.bind(this);
    }
    handleSubmit(event) {
        
        event.preventDefault();
        const { form } = this.props.state;
        const memodata = 'bet:'.concat(form.numbers,':',form.period);
        this.setState({returnobj:{passinf:'Loading'},pop_show:true})
        
        return ApiService.bet(memodata)
            .then((mesg) => {
                console.dir(mesg);
                this.setState({returnobj:mesg.processed})
                this.props.reset_userinf();
            })
            .catch(err => {
                console.dir(err);
                console.dir(memodata);
                this.props.reset_userinf();
            });
    }

    render(){
        if(! sessionStorage.getItem("User_EOS_key")){
            return(
                <p>please set user</p>
            );
        }
        else{
            const { form, error } = this.props.state;
            return(
                
                <div id="bet_block">
                <Popoutwindow show={this.state.pop_show} title={"Transaction Information"} handleClose={()=>this.setState({pop_show:false})} console_obj={this.state.returnobj}/>
                <Form onSubmit={this.handleSubmit}>
                    <Form.Group >
                        <Form.Label>Numbers</Form.Label>
                        <Form.Control name="numbers" type="string" placeholder="1~39 ,ex:1,2,3,4,5" autoComplete="off" onChange={this.props.handleChange} value={form.numbers}/>
                        <Form.Text className="text-muted">
                            chose 5 numbers in 1~39,and split it by ','
                        </Form.Text>
                    </Form.Group>

                    <Form.Group controlId="formBasicPassword">
                        <Form.Label>Period</Form.Label>
                        <Form.Control name="period" type="number" placeholder="period" autoComplete="off" onChange={this.props.handleChange} value={form.period}/>
                    </Form.Group>
                    
                    <Button variant="primary" type="submit">
                        Submit
                    </Button>
                </Form>
                </div>
            );
        }
    }
}
export default ActionBet;